import React, {useEffect, useState} from 'react';
import {AppBar, Toolbar, IconButton, makeStyles, Typography, Button, MenuItem, Menu} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import {useLocation} from 'react-router-dom';
import {useDispatch} from 'react-redux';
import {saveAsFile, setShowPreview} from '../features/Lightbar/ligther-slice';


const useStyles = makeStyles(theme => ({
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  previewButton: {
    color: '#ffffff',
    borderColor: '#ffffff',
  }
}));

function Header() {
  const classes = useStyles();
  const location = useLocation();
  const [showAppbarButtons, setShowAppbarButton] = useState(false)
  const [anchorEl, setAnchorEl] = React.useState(null);
  const dispatch = useDispatch();

  const onCloseContexMenu = (isJSON) => () => {
    if (isJSON !== null) {
      dispatch(saveAsFile(isJSON));
    }
    setAnchorEl(null);
  };
  const openContextMenu = e => setAnchorEl(e.currentTarget);
  const onPreviewClick = () => dispatch(setShowPreview(true));

  useEffect(() => {
    if (location.pathname.indexOf('lightbar') > 0) {
      setShowAppbarButton(true);
    } else {
      setShowAppbarButton(false);
    }
  }, [location]);

  return (
    <AppBar position="static">
      <Toolbar>
        <IconButton edge="start" color="inherit" aria-label="Menu">
          <MenuIcon />
        </IconButton>
        <Typography variant="h6" className={classes.title}>
          DandR Electronic Lighbar Configurator
        </Typography>
        { showAppbarButtons &&
          <>
            <Button
              className={classes.previewButton}
              onClick={onPreviewClick}
              startIcon={<PlayArrowIcon />}
              variant="outlined"
            >
              Preview
            </Button>
            <IconButton
              aria-label="Download"
              color="inherit"
              edge="end"
              onClick={openContextMenu}
            >
              <MoreVertIcon />
            </IconButton>
          </>
        }
        <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={onCloseContexMenu()}
        >
          <MenuItem onClick={onCloseContexMenu(false)}>Export BIN</MenuItem>
          <MenuItem onClick={onCloseContexMenu(true)}>Export JSON</MenuItem>
        </Menu>
      </Toolbar>
    </AppBar>
  );
}

export default Header;
