import React from 'react';
import { Card, CardContent, IconButton, makeStyles, CardHeader } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import { blue } from '@material-ui/core/colors';

import CycleList from './cycle-list';
import {useDispatch} from 'react-redux';
import {addCycle} from '../ligther-slice';

const useStyles = makeStyles({
  action: {
    color: '#ffffff',
  },
  card: {
  },
  cardHeader: {
    color: '#ffffff',
    backgroundColor: blue[500],
  },
  root: {
  },
});

function Cycles() {
  const classes = useStyles();
  const dispatch = useDispatch();

  const onclick = () => dispatch(addCycle());

  return (
    <div className={classes.root}>
      <Card className={classes.card}>
        <CardHeader className={classes.cardHeader}
          action={
            <IconButton
              arial-label="Add a new cycle"
              className={classes.action}
              onClick={onclick}
            >
              <AddIcon />
            </IconButton>
          }
          title="Cycles"
        />
        <CardContent>
          <CycleList />
        </CardContent>
      </Card>
    </div>
  );
}

export default Cycles;
