import React, {useRef, useState, useEffect} from 'react';
import { makeStyles } from '@material-ui/core';
import { Stage, Layer, Rect, Text } from 'react-konva';

import { rgbToHex } from '../../app/utils';


const useStyles = makeStyles({
  root: {
    display: 'flex',
    flexDirection: 'row',
    height: props => props.width ? 'auto' : 200,
    justifyContent: 'center',
    marginLeft: 10,
    maxWidth: 750,
    minWidth: props => props.width || 500,
    width: props => props.width || '100%',
  },
});

function LightbarPreview({ lights = [], onClick, selectedLight, title, width }) {
  const classes = useStyles({ width });
  const containerRef = useRef();

  if (!lights.length) {
    return null;
  }

  const front = lights.slice(0, (lights.length - 6) / 2);
  const rear = lights.slice(front.length + 3, -3);
  const originalWidth = (57 * 2) + (front.length * 45);
  const scale = (width || originalWidth) / originalWidth;
  const lightbarWidth = front.length * (45 * scale);
  const shadowBlur = 5 * scale;

  // TODO it doesn't work properly
  // useEffect(() => {
  //   if (containerRef.current.offsetWidth !== divWidth
  //     && containerRef.current.offsetWidth <= 750) {
  //       setDivWidth(containerRef.current.offsetWidth);
  //     }
  //   },
  // );
  
  const onLightClick = (id) => () => onClick && onClick(id);

  const getStrokeColor = (id) => selectedLight === id ? 'red' : null;

  const getColor = ({r, g, b}) => `#${rgbToHex({r, g, b})}`;

  return (
    <div ref={containerRef} className={classes.root}>
      <Stage width={originalWidth * scale} height={200 * scale}>
        <Layer x={57 * scale}>
          {
            front.map((l, index) =>
              <Rect
                key={index}
                x={45 * index * scale}
                y={20 * scale}
                width={40 * scale}
                height={24 * scale}
                fill={getColor(l.color)}
                shadowBlur={shadowBlur}
                cornerRadius={5}
                onClick={onLightClick(index)}
                stroke={getStrokeColor(index)}
              />
            )
          }
        </Layer>
        <Layer rotation={-180} x={(57 * 2 * scale) + lightbarWidth} y={184 * scale}>
          <Rect
            x={58 * scale}
            y={44 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[front.length + 2].color)}
            shadowBlur={shadowBlur}
            rotation={135}
            cornerRadius={5}
            onClick={onLightClick(front.length + 2)}
            stroke={getStrokeColor(front.length + 2)}
          />
          <Rect
            x={30 * scale}
            y={73 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[front.length + 1].color)}
            shadowBlur={shadowBlur}
            rotation={90}
            cornerRadius={5}
            onClick={onLightClick(front.length + 1)}
            stroke={getStrokeColor(front.length + 1)}
          />
          <Rect
            x={30 * scale}
            y={113 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[front.length].color)}
            shadowBlur={shadowBlur}
            rotation={45}
            cornerRadius={5}
            onClick={onLightClick(front.length)}
            stroke={getStrokeColor(front.length)}
          />
        </Layer>
        <Layer x={57 * scale}>
          {
            rear.map((l, index) =>
              <Rect
                key={index}
                x={45 * index * scale}
                y={140 * scale}
                width={40 * scale}
                height={24 * scale}
                fill={getColor(lights[lights.length - 4 - index].color)}
                shadowBlur={shadowBlur}
                cornerRadius={5}
                onClick={onLightClick(lights.length - 4 - index)}
                stroke={getStrokeColor(lights.length - 4 - index)}
              />
            )
          }
        </Layer>
        <Layer>
          <Rect
            x={55 * scale}
            y={44 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[lights.length - 1].color)}
            shadowBlur={shadowBlur}
            rotation={135}
            cornerRadius={5}
            onClick={onLightClick(lights.length - 1)}
            stroke={getStrokeColor(lights.length - 1)}
          />
          <Rect
            x={27 * scale}
            y={73 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[lights.length - 2].color)}
            shadowBlur={shadowBlur}
            rotation={90}
            cornerRadius={5}
            onClick={onLightClick(lights.length - 2)}
            stroke={getStrokeColor(lights.length - 2)}
          />
          <Rect
            x={27 * scale}
            y={113 * scale}
            width={40 * scale}
            height={24 * scale}
            fill={getColor(lights[lights.length - 3].color)}
            shadowBlur={shadowBlur}
            rotation={45}
            cornerRadius={5}
            onClick={onLightClick(lights.length - 3)}
            stroke={getStrokeColor(lights.length - 3)}
          />
          </Layer>
      </Stage>
    </div>
  );
}

export default LightbarPreview;
