import React from 'react';
import { List, makeStyles, ListItem, ListItemText, ListItemSecondaryAction, IconButton } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import {useSelector, useDispatch} from 'react-redux';
import {getCycles, getSelectedCycle, setSelectedCycle, deleteCycle} from '../ligther-slice';
import LightbarPreview from '../../LightbarPreview';

const useStyles = makeStyles({
  root: {
    width: '100%',
    overflow: 'auto',
    maxHeight: 400,
  },
  item: {
    height: 75,
  }
});

const Cycle = ({
  className,
  id,
  lights,
  onClick,
  onDelete,
  selected
}) => (
  <ListItem
    alignItems="center"
    className={className}
    selected={selected}
    onClick={onClick}
    button>
    <ListItemText 
      primary={ `Cycle #${id + 1}` }
    />
    <LightbarPreview lights={lights} width={(lights.length - 6) * 10} />
    <ListItemSecondaryAction>
      <IconButton
        arial-label="delete"
        edge="end"
        onClick={onDelete}
      >
        <DeleteIcon />
      </IconButton>
    </ListItemSecondaryAction>
  </ListItem>
);

function CycleList() {
  const classes = useStyles();
  const cycles = useSelector(getCycles);
  const selectedCycle = useSelector(getSelectedCycle);
  const dispatch = useDispatch();

  const onClick = (id) => () => dispatch(setSelectedCycle(id));
  const onDelete = (id) => () => dispatch(deleteCycle(id));

  return (
    <List className={classes.root}>
      {
        cycles.map((c, i) => <Cycle
          className={classes.item}
          key={i}
          id={i}
          lights={c.lights}
          onClick={onClick(i)}
          onDelete={onDelete(i)}
          selected={i === selectedCycle}
        />)
      }
    </List>
  );
}

export default CycleList;
