import React, {useState} from 'react';
import { Card, CardContent, CardHeader, TextField, Typography, makeStyles, Button } from '@material-ui/core';
import {blue} from '@material-ui/core/colors';

import ColorPicker from './color-picker';

const useStyles = makeStyles({
  root: {
    minWidth: 400,
    maxWidth: 500,
  },
  card: {},
  cardHeader: {
    color: '#ffffff',
    backgroundColor: blue[500],
  },
  cardContent: {},
  colorInfo: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
  colorView: {
    width: 25,
    height: 25,
    backgroundColor: prop => `rgb(${Object.values(prop.color)})`,
  }
});

function LightbarSetting({ config, onChange }) {
  const classes = useStyles({ color: config?.color || { r: 255, g: 255, b: 255 } });
  const [showColorPicker, setShowColorPicker] = useState(false);

  const onValueChange = ({ target }) => {
    console.log(target.value);
    onChange({
      color: {
        ...config?.color,
        [target.name]: +target.value >= 0 && +target.value <= 255 ?
          target.value.trim()
          : config?.color[target.name]
      }
    });
  };

  const onColorPickerChange = (color) => onChange({
    color: delete color.rgb.a && color.rgb,
  });

  const setColorPickerState = () => setShowColorPicker(!showColorPicker);

  return (
    <div className={classes.root}>
      <Card className={classes.card}>
        <CardHeader
          className={classes.cardHeader}
          title="Set the color"
        />
        <CardContent className={classes.cardContent}>
          { !!config ?
            <div className={classes.colorInfo}>
              <TextField
                label="Red"
                inputProps={{ max: 255, min: 0 }}
                name="r"
                onChange={onValueChange}
                size="small"
                type="number"
                value={config?.color?.r || 255}
                variant="outlined"
              />
              <TextField
                label="Green"
                inputProps={{ max: 255, min: 0 }}
                name="g"
                onChange={onValueChange}
                size="small"
                type="number"
                value={config?.color?.g || 255}
                variant="outlined"
              />
              <TextField
                label="Blue"
                inputProps={{ max: 255, min: 0 }}
                name="b"
                onChange={onValueChange}
                size="small"
                type="number"
                value={config?.color?.b || 255}
                variant="outlined"
              />
              <Button
                color="default"
                onClick={setColorPickerState}
                variant="contained"
              >
                <span className={classes.colorView} />
              </Button>
              {
                showColorPicker &&
                  <ColorPicker
                    value={config?.color}
                    onClick={setColorPickerState}
                    onChange={onColorPickerChange}
                  />
              }
            </div>
            :
            <Typography variant="h6">
              Please select a light module from the lightbar preview to set the color.
            </Typography>
          }
        </CardContent>
      </Card>
    </div>
  );
}

export default LightbarSetting;
